import React from "react";
import Card from "../Card/Card";
import importAllImagesText from '../Helpers/ImportAllImagesText';

const barsImages = await importAllImagesText(require.context('../PartnersLogos/Bars', false, /\.(webp)$/));

const Bars = () => {
    return (
        <>
            <h2>Bars</h2>
            {!barsImages.length && <h4 style={{textAlign:'center'}}>No Partners Yet!</h4>}
            <div className="section-container">
                {barsImages.length > 0 &&  barsImages.map((barImage, index) => (
                    <Card
                        classAdded={"card-for-all"}
                        key={index}
                        image={barImage.image}
                        partner_name={barImage.name}
                    />

                ))}
            </div>
        </>
    );
}

export default Bars;